@import "../scss/variables";

ul.resp-tabs-list {
	margin: 0px;
	padding: 0px;
}
.resp-tabs-list li {
	font-weight: 600;
	font-size: 13px;
	display: inline-block;
	padding: 13px 15px;
	margin: 0 0px 0 4px;
	list-style: none;
	cursor: pointer;
	float: right;
}
.resp-tabs-container {
	padding: 0px;
	background-color: transparent;
	clear: left;
}
h2.resp-accordion {
	cursor: pointer;
	padding: 5px;
	display: none;
}
.resp-tab-content {
	display: none;
	padding: 15px;
	padding-bottom: 0;
}
.resp-tab-active {
	border: 1px solid #8645ff !important;
	border-bottom: none;
	margin-bottom: 0px !important;
	padding: 12px 14px 14px 14px !important;
	border-bottom: 0 !important;
	border-bottom: none;
	background-color: #fff;
}
.resp-content-active, .resp-accordion-active {
	display: block;
}
.resp-tab-content {
	border: 1px solid #c1c1c1;
	border-top-color: #8645ff;
}
h2 {
	&.resp-accordion {
		font-size: 13px;
		border: 1px solid #c1c1c1;
		border-top: 0px solid #c1c1c1;
		margin: 0px;
		padding: 10px 15px;
	}
	&.resp-tab-active {
		border-bottom: 0px solid #c1c1c1 !important;
		margin-bottom: 0px !important;
		padding: 10px 15px !important;
	}
	&.resp-tab-title:last-child {
		border-bottom: 12px solid #c1c1c1 !important;
		background: blue;
	}
}
/*-----------Vertical tabs-----------*/

.resp-vtabs {
	ul.resp-tabs-list {
		float: none;
		margin-top: 0 !important;
		background: #fff;
	}
	.resp-tabs-list li {
		display: block;
		padding: 10px 0px !important;
		margin: 0 0 4px;
		cursor: pointer;
		float: none;
		margin: 0 auto;
		text-align: center;
		background-color: transparent !important;
		border: 1px solid rgba(107, 122, 144, 0.1) !important;
		border-top: 0 !important;
		border-bottom: 0;
		border-left: 1px solid #e9ecf3 !important;
	}
	.resp-tabs-container {
		padding: 0px;
		background-color: transparent;
		clear: none;
		border-bottom: 0 !important;
	}
	.resp-tab-content {
		border: none;
		word-wrap: break-word;
		height: 100%;
	}
	li {
		&.resp-tab-active {
			position: relative;
			z-index: 1;
			margin: 0 auto;
			text-align: center;
			border-top: 1px solid;
			border-right: 0 !important;
			background: #fff;
			color: #5e2dd8;
			border-right: 0px !important;
		}
		&.active.resp-tab-item {
			background: #fff !important;
			color: #5e2dd8;
		}
	}
}
.first-sidemenu .resp-tabs-list li.resp-tab-active {
	border-left: 0px !important;
}
.resp-vtabs li:hover {
	background: transparent;
}
.resp-arrow {
	width: 0;
	height: 0;
	float: right;
	margin-top: 3px;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 12px solid #c1c1c1;
}
h2.resp-tab-active {
	span.resp-arrow {
		border: none;
		border-left: 6px solid transparent;
		border-right: 6px solid transparent;
		border-bottom: 12px solid #9B9797;
	}
	background: #DBDBDB;
	/* !important;*/
}
/*-----------Accordion styles-----------*/

.resp-easy-accordion {
	h2.resp-accordion {
		display: block;
	}
	.resp-tab-content {
		border: 1px solid #c1c1c1;
		&:last-child {
			border-bottom: 1px solid #c1c1c1;
			/* !important;*/
		}
	}
}
.resp-jfit {
	width: 100%;
	margin: 0px;
}
.resp-tab-content-active {
	display: block;
}
h2.resp-accordion:first-child {
	border-top: 1px solid #c1c1c1;
	/* !important;*/
}